<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <button @click="to_test">去实验页面</button>
    <button @click="to_prop">去props页面</button>
    <button @click="to_home">去home页面</button>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  methods:{
    to_test(){
      // this.$routes.path="/index";
      this.$router.push({path: '/index'})
    },
    to_prop(){
       this.$router.push({path: '/parent'})
    },
    to_home(){
      this.$router.push({path: '/home'})
    }
  },
  watch:{
        $route(to,from){
            console.log(to,from);
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



