<template>
    <div class="index">
        <ul v-for="(item,id) in sups" :key="id">
            <li  @click="getid(id)">企业：{{item.name}}</li>
            <li>员工数：{{item.people}}</li>
            <li>行业：{{item.field}}</li>
        </ul>
        <div class="box1" ref="box1" id="box1" @click="c_color"></div>
        <div>获取焦点：<input type="text" @focus="to_input"></div>
        <div class="news">
            <router-view></router-view>
        </div>
    </div>
</template>
<script>
export default {
    name:'Index',
    data(){
        return{
            sups:[
                {name:'腾讯', people:'8000',field:'IT'},
                {name:'华为', people:'12000',field:'通信电子'},
                {name:'大疆', people:'4000',field:'无人机'},
                {name:'联想', people:'7000',field:'电脑'},
                {name:'索尼', people:'9000',field:'相机'},
                {name:'索尼', people:'9000',field:'相机'},
            ],
            gcolor:'true',
        }
    },
    methods:{
        getid(iid){
            console.log(iid);
        },
        to_input(){
            this.$router.push({path:'/inword'})
        },
        c_color(){
            let a = this.$refs.box1;
            this.gcolor = !this.gcolor;
            console.log(this.gcolor);
            if(this.gcolor == false){
                a.style.backgroundColor = 'green'
            }
            if(this.gcolor == true){
                a.style.backgroundColor = 'red'
            }
            // let a = document.getElementById("box1");
            // var style = window.getComputedStyle(a);
            // let a = this.$refs.box1;
            // a.style.backgroundColor = 'green' || 'red';
            // console.log(style.backgroundColor)
        }
    },
    // watch:{
    //    "$route"(to,from){
    //        console.log(to);
    //    }
    // },
    mounted(){
        // let a = this.item.id;
        // console.log(a);
        // this.$router.beforeEach((to, from, next) => {
        //     console.log(from.name);
        //     next();
        // });
        // console.log(this.gcolor)
    },
    beforeRouteEnter (to, from, next) {
        // style.backgroundColor = 'green';
        // console.log(a)
        next();
    }
}
</script>
<style lang="less">
.index{
     .news{
      width:400px;
      height: 800px;
      border: solid red 1px;
      border-radius: 4px;
      margin: 0 auto;
      overflow-y: scroll;
      overflow-x: hidden;
    }
    .box1{
        width: 50px;
        height: 50px;
        border-radius: 50%;
        background-color: red; 
    }
}
 
</style>



