import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/page/HelloWorld'
import Index from '@/page/Index'
import News from '@/page/news/News'
import NewsItem from '@/page/news/NewsItem'
import parent from '@/components/parent'
import inword from '@/components/inword'
import home from '@/page/home'
import one from '@/page/homeA/one'
import two from '@/page/homeA/two'
import three from '@/page/homeA/three'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/index',
      name: 'index',
      component: Index,
      children:[
        {
          path: '/index',
          name: 'News',
          component: News,
          children:[
            // {
            //   path: '/index/NewsItem',
            //   name: 'NewsItem',
            //   component: NewsItem
            // },
          ]
        },
        {
          path: '/index/NewsItem',
          name: 'NewsItem',
          component: NewsItem
        },
      ]
    },
    {
      path: '/parent',
      name: 'parent',
      component: parent
    },
    {
      path: '/inword',
      name: 'inword',
      component: inword
    },
    {
      path: '/home',
      name: 'home',
      component: home,
      children:[
        {
          path: '/home/one',
          name: 'one',
          component: one
        },
        {
          path: '/home/two',
          name: 'two',
          component: two
        },
        {
          path: '/home/three',
          name: 'three',
          component: three
        },
      ]
    },
  ]
})



